@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat+Subrayada:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@700;800&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;

}

.main-container {
  background-color: #0B151F ;
  min-height: 150px;

}
.main__container__bannerInnerItem {
  width: 100%;
  min-height: 1080px;
  position: absolute;
  top:0;
  z-index: 1;
  background-position: top;
  background-size: cover;
}
/*header color #573FC6 */
  
  .form-img {
    width: 80%;
    height: 80%;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }


  .form-inputs .form-input {
    border: 1px solid #573FC6;
  }

  .form-img-2 {
    width: 60%;
    height: 60%;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
  
  .form-success {
    text-align: center;
    font-size: 24px;
    margin-top: 80px;
    color: #fff;
  }
  
  .form-content-right {
    border-radius: 0 10px 10px 0;
    position: relative;
    background: linear-gradient(90deg, rgb(40, 40, 40) 0%, rgb(17, 17, 17) 100%);
  }
  

  
  .form h1 {
    font-size: 1rem;
    text-align: start;
    width: 80%;
    margin-bottom: 1rem;
    color: #fff;
  }
  
  .form-inputs {
    margin: 0.5rem auto;
    width: 80%;
  }
  
  .form-inputs p {
    font-size: 0.8rem;
    margin-top: 0.5rem;
    color: #f00e0e;
  }
  
  .form-label {
    display: inline-block;
    font-size: 0.8rem;
    margin-bottom: 6px;
    color: rgb(68, 65, 65);
  }
  
  .form-input {
    display: block;
    padding-left: 10px;
    outline: none;
    border-radius: 2px;
    height: 40px;
    width: 100%;
    border: none;
  }
  
  .form-input::-webkit-input-placeholder {
    color: #595959;
    font-size: 12px;
  }
  
  .form-input:-ms-input-placeholder {
    color: #595959;
    font-size: 12px;
  }
  
  .form-input::placeholder {
    color: #595959;
    font-size: 12px;
  }
  
  .form-input-btn {
    width: 80%;
    height: 50px;
    margin-top: 10px;
    border-radius: 2px;
    background: linear-gradient( 
        90deg
        ,rgb(74, 168, 110) 0%, #479266 100% );
       

    transition: all 0.4s ease-out;
   
    outline: none;
    border: none;
    color: #fff;
    font-size: 1rem;
  }
  
  .form-input-btn:hover {
    cursor: pointer;
    background: linear-gradient( 
        111deg
        ,rgb(67,103,167) 0%, #2d4998 100% );
    transition: all 0.4s ease-out;
  }
  
.container-for-footer {
        display: flex;
        flex-wrap: wrap;
        flex-shrink: 0;
        align-items: center;
        justify-content: space-around;
        padding: 1.75rem;
        border-top: 1px solid #dee2e6;
        border-bottom-right-radius: calc(.3rem - 1px);
        border-bottom-left-radius: calc(.3rem - 1px);
}

  .form-input-login {
    font-size: 0.8rem;
    margin:10px auto;
    color: rgb(138, 130, 130);
    width: 80%;
    text-align: center;
  }
  



  .form-input-login a {
    text-decoration: none;
    color: #6438df;
    font-weight: 600;
  }
  
  .modal-content {
      border-radius: 8px;
  }
.header__nav {
    display: flex;
    justify-content: flex-end;
    padding: 0 10px;
    margin-right: 3em;
    color:#F0E9E9;
   


}

.header {

    position: absolute;  
    top: 0;  
    z-index: 5; 
    min-height: 70px;
    font-size: 30px;
  
}


.header .show {
    flex-direction: column;
     align-items:space-around;
    justify-content: space-around;
    background-color: #0B151F;
}

.header .nav__link__simple {
    text-decoration: none;
    color:#F0E9E9

}
.header .navbar-brand {
    color:#F0E9E9;
    font-size: 1.5em;
    margin-left: 2em;

}

.header .nav__link__enter,.header .nav__link__out {
text-decoration: none;
color:#F0E9E9
}

.header .nav-link>a:hover {
    text-shadow: 1px 0 black;
    transition: text-shadow 0.5s;
    color:#96EB9E;
    text-decoration: underline;
}

.header .navbar-brand:hover {
    color:#F0E9E9;
}
@media screen and (max-width:1200px){
    .header .navbar-brand  {
        font-size: 0.75em;
        margin-left: 1em;
    }
    .header__nav{
        margin-right: 1em;
    }
    .header .header__nav__right {
        font-size: 0.5em;
       
    }
   
    .navbar-toggler-icon {
     
    }
}

@media screen and (max-width:992px){

    .header .header__nav  {
        flex-direction: column;
        align-items:space-around;
    }

    .header .header__nav__right {
        min-width: 90vw;
        padding: 0;
     
    }
    .header  .show .header__nav__right>.nav-link {
        min-width: 90vw;
       border-bottom: 1px solid gray;
      
    }

    .nav-link{
        text-align: center;
    }

}
    

body {
  background-color: #EAEAEA;
} 

.coin__app {
  padding-top: 3em;
  color: #F0E9E9(46, 43, 43);
}


.coin__search {
  margin-top: 4em;
  margin-bottom: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  color:whitesmoke;
  border-top: 1px solid #96EB9E;
  padding-top: 2em;
}


.coin__text {
  margin-bottom: 1rem;
  text-align: center;
}

.coin__input {
  padding-left: 16px;
  max-width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient( 
    -225deg
    , #0eb38d 0%, #00000088 48%, #373aff 100% );

  color: #f3eaea;
  font-weight: 600;
}

.coin__input::-webkit-input-placeholder {
  color: #ffffff8f;
}

.coin__input:-ms-input-placeholder {
  color: #ffffff8f;
}

.coin__input::placeholder {
  color: #ffffff8f;
} 


.table__header th>h4{
  display: flex;
  justify-content: space-around;
text-align: center;
}
.coin__row td>p {
  display: flex;
  justify-content: space-around;
 
}

.coin__row{
  color:#F0E9E9;

}
.coin__name {
  font-weight: bold;
  white-space: nowrap;
  min-width: 85px;
  max-width: 200px;
  text-align: center;
}
.coin__name>p {
  min-width: 85px;
 
}
.coin__img {
  min-height: 100%;
  max-width: 30px;

 
}
.coin__table {
  color:#F0E9E9;

}
  
  .coin__img img {
    max-height: 30px;
    max-width: 30px;
    margin-right: 1em;
    
  }
  
  .coin__symbol {
    text-transform: uppercase;
  }


  .red {
    color: #f00606;
  }
  
  .green {
    color: #11d811;
  }
   

@media screen and (max-width:1365px) {
  .coin__name {
    max-width: 100px;
    white-space:pre-line;
   
  }
    .coin__name>p {
      min-width: 85px;
      max-width:100px;
    }
}
  

.home__container {
margin-top: 100px;
color: whitesmoke;

}

.home__row {
display: grid;
grid-template-columns: 80% 20%;

}
.home__container__right h3 {
    font-family: Montserrat;

    font-weight: 700;
    font-size: 2em;
    line-height: 150.9%;
    letter-spacing: 0.12em;

}
.home__container__right {
   
    min-height: 300px;
    text-align: left;
    display: flex;
    align-items: flex-end;
  
 
}

.home__button {
    background: #96EB9E;
    border-radius: 15px; 
    width: 180px;  
    height: 40px;
    border: none;
    margin: 0 55%;
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
   
}

.home__container__right .home__button a {
    text-decoration: none;
    color: black;
}

.home__button:hover {
    background: #565a95;
    color:blanchedalmond;
}

@media screen and (min-width: 1900px) {
    .home__container__right h3{
        min-width: 500px;
    }
    .home__container__right span {
        white-space: nowrap;
    }
    .home__row {
        display: grid;
        grid-template-columns: 70% 30%;

        }
}
@media (min-width:1365px) and (max-width: 1900px){  
    .home__container__right span {
        white-space: nowrap;
    }
    .home__row {
        font-size: 15px;
        grid-template-columns: 70% 30%;
    }

    .home__button {
    
        margin: 0 46%;
      
    }
}
@media screen and (max-width:1364px) {
    .home__container__right{
        font-size: 12px;
       
    }
    .home__row {
        font-size: 15px;
        grid-template-columns: 60% 40%;
    }
    .home__button {
    
        margin: 0 33%;
      
    }

}

@media screen and (max-width:1023px) and (max-height:400px) {
    .home__button {
        margin: 0 auto;
    }
}

@media screen and (max-width:767px) {
    .home__row {
       
        grid-template-columns: 0 100%;
    }

    .home__container__right{
        font-size: 10px;
    }

    .home__container__right {
   
        min-height: 265px;
        text-align: center;
        display: flex;
        align-items:flex-end;
      
    }
    .home__button {
        margin: 0 auto;
    }
 
}

